import React, {useEffect, useState} from "react";
import AccountContainer from "./AccountContainer";

function App() {
  const [transactions, setTransactions] = useState([]);

  function addNewTransaction(newTransactionObj) {

    fetch("http://localhost:8001/transactions", {
      method: 'POST',
      headers: {
      'Content-Type': 'application/json',
      },
      body: JSON.stringify(newTransactionObj)
    })
    .then(r => r.json())
            .then(data => setTransactions(data));
  }
  
  useEffect(() => {
    fetch("https://github.com/Mutuma-Gitonga/phase-2-code-challenge-1/blob/mutuma/db.json")
      .then((r) => r.json())
        .then(data => setTransactions(data));
  },[])

  function searchItem(word) {

    const matchingTransactions = transactions.filter(transaction => transaction.description.includes(word));

    setTransactions(matchingTransactions);
  }

  return (
    <div className="ui raised segment">
      <div className="ui segment violet inverted">
        <h2>The Royal Bank of Flatiron</h2>
      </div>
      <AccountContainer searchItem={searchItem} onSubmit={addNewTransaction} transactions={transactions} />
    </div>
  );
}

export default App;
